export default class LanguageClass {
    static WHO_ARE_YOU = Symbol();
    static ZHANBU_WHAT = Symbol();
    static INTERVIEW = Symbol();
    static BOY_GIRL = Symbol();
    static FATE = Symbol();
    static NICE = Symbol();
    static analyze(txt) {
      if(txt == "" || /[]/.test(txt)){
        return null;
      }
      let analyzeDatalist = new Map([
        [/^[]*[]*$/, LanguageClass.WHO_ARE_YOU],
        [/^[]*[]*[]*[]*[]*$/, LanguageClass.ZHANBU_WHAT],
        [/^[]*[]*[]*$/, LanguageClass.INTERVIEW],
        [/^[]*[]*[]*$/, LanguageClass.INTERVIEW],
        [/^[(\S{1,4})]*[]*[]*[]*$/, LanguageClass.BOY_GIRL],
        [/^[(\S{1,4})]*[]*[]*[]*$/, LanguageClass.BOY_GIRL],
        [/^[(\S{1,4})]*[]*$/, LanguageClass.FATE],
        [/^[]*[]*[]*$/, LanguageClass.NICE]
      ]);
      for (let [key, value] of analyzeDatalist) {
        if(key.test(txt)){
          return value;
        }
      }
      return null;
    }
}
  