<template>
  <div class="content">
    <img class="btn-back" @click="goBack" src="../assets/back_off.png">
    <div style="display:none;">
      <button @click="test1">test</button>
      <button @click="test2">test2</button>
      <button @click="test3">test3</button>
      <button @click="test4()">test4</button>
      <button @click="test5()">test5</button>
    </div>
    <div class="qx-con" ref="box" @click="ask">
    </div>
    <div class="c-que-list" v-show="queShow">
      <div @click="doZhanbu(3)"></div>
      <div @click="doZhanbu(2)"></div>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import QX_Normal from '@/components/qx_normal.js'
import QXContent from "./QXContent"
import fateData from "@/components/fateData.js";

export default {
  name: 'normal-content',
  mixins: [QXContent],
  data() {
    return {
        queShow: false,
        //
        lanList: {
          que: require("../assets/s/que.mp3"),
          result: require("../assets/s/result.mp3"),
          yunqi: require("../assets/s/yunqi.mp3"),
          nannv: require("../assets/s/nannv.mp3"),
        }
    }
  },
  methods: {
    init() {
      this.qx = new QX_Normal({el: this.$refs.box});
      this.addMainObj();
      this.addEvent();
    },
    //
    addEvent() {
      this.onPauseFun = ()=> {
        this.onBackground = true;
      }
      this.onResumeFun = ()=> {
        this.onBackground = false;
      }
      document.addEventListener('pause', this.onPauseFun, false);
      document.addEventListener('resume',this.onResumeFun, false);
    }, 
    beforeDestroy() {
      document.removeEventListener('pause', this.onPauseFun, false);
      document.removeEventListener('resume',this.onResumeFun, false);
      this.stopZhanbu();
      this.qx.destroy();
    },
    async ask() {
      if(this.onBackground) {
        return;      
      }
      if(this.starting) {
        return;
      }
      this.starting = true;
      this.showImage("images/eye.png");
      await this.playSound(this.lanList.que);
      this.listen();
    },    
    listen() {
      this.queShow = true;
    },  
    async doZhanbu(result) {
        //
        if(result == 2) {
          this.zhanbu_boygirl();
        }
        //
        else if(result == 3) {
          this.zhanbu_fate();
        }
        this.queShow = false;
    },  
    //
    async zhanbu_boygirl() {
      await this.playSound(this.lanList.nannv);
      await this.startZhanbu();
      this.playSound(this.lanList.result);
      this.showImage(this.getBoygirl());
      this.stopZhanbu_timeout();
    }, 
    async zhanbu_fate() {
      await this.playSound(this.lanList.yunqi);
      await this.startZhanbu();
      let n = parseInt(Math.random() * fateData.length);
      this.playSound(this.lanList.result);
      this.showImage("images/fate/" + fateData[n][1] + ".png");
      this.stopZhanbu_timeout();
    },            
  }
}
</script>
<style scoped lang="scss">
  .content{
    background: #000000;
    height: 100%;
  }
  .qx-con{
    width: 100vw;
    height: 100vw;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .c-que-list {
    position:fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 12vh;
    text-align: center;
    width: 80%;
    div {
      display: inline-block;
      font-size: 20px;
      margin: 10px 20px;
      border: solid 2px #CCCCCC;
      border-radius: 4px;
      color: #CCCCCC;
      box-shadow: 0 0 5px #f5f5f5;
      padding: 6px 8px;
    }
  }
 
</style>
