let animate = require('gsap-promise');

export default class QX {
  constructor(arg){
    this.el = arg.el;
    this.renderListenerArr = new Set();
    this.scene = new THREE.Scene();
    this.modelPath = "model/";
    this.imagePath = "images/";
    this.initLight();
    this.initCamera();
    this.initRender();
    this.animate();
  }

  initCamera() {
    this.camera_top = new THREE.PerspectiveCamera(60,window.innerWidth/(window.innerWidth/2),0.1,1000);
    this.camera_top.position.x = -120;
    this.camera_top.up =  new THREE.Vector3( 0, 1, 0 );
    this.camera_top.lookAt(this.scene.position); 

    this.camera_right = new THREE.PerspectiveCamera(60,(window.innerWidth/2)/window.innerWidth,0.1,1000);
    this.camera_right.position.z = 240 
    this.camera_right.up =  new THREE.Vector3( 0, 0, 1 );
    this.camera_right.lookAt(this.scene.position);

    this.camera_bottom = new THREE.PerspectiveCamera(60,window.innerWidth/(window.innerWidth/2),0.1,1000);

    this.camera_bottom.position.x = 120;
    this.camera_bottom.up =  new THREE.Vector3( 0, -1, 0 );
    this.camera_bottom.lookAt(this.scene.position);

    this.camera_left = new THREE.PerspectiveCamera(60,(window.innerWidth/2)/window.innerWidth,0.1,1000);  
    this.camera_left.position.z = -240; 
    this.camera_left.up =  new THREE.Vector3( 0, 0, -1 );
    this.camera_left.lookAt(this.scene.position); 
  }

  initRender() {
    this.renderer_top = new THREE.WebGLRenderer({antialias: true});
    this.renderer_top.setSize(window.innerWidth,window.innerWidth/2);
    this.el[0].appendChild(this.renderer_top.domElement);

    this.renderer_right = new THREE.WebGLRenderer({antialias: true});
    this.renderer_right.setSize(window.innerWidth/2,window.innerWidth);
    this.el[1].appendChild(this.renderer_right.domElement);

    this.renderer_bottom = new THREE.WebGLRenderer({antialias: true});
    this.renderer_bottom.setSize(window.innerWidth,window.innerWidth/2);
    this.el[2].appendChild(this.renderer_bottom.domElement);

    this.renderer_left = new THREE.WebGLRenderer({antialias: true});
    this.renderer_left.setSize(window.innerWidth/2,window.innerWidth);
    this.el[3].appendChild(this.renderer_left.domElement);
  }

  initLight() {
    // //
    this.ambientLight = new THREE.AmbientLight(0xcccccc, .3);
    this.scene.add(this.ambientLight);
    //
    this.pointLight = new THREE.PointLight(0xffffff, .8);
    this.pointLight.position.z = 60;
    this.pointLight.position.y = 60;
    this.scene.add(this.pointLight);
    //console.log(this.pointLight.intensity);
  }

  brighten() {
    animate(this.pointLight, 1, { intensity: 0.9 });
    animate(this.ambientLight, 1, { intensity: 0.5 });
  }

  darken() {
    animate(this.pointLight, 1, { intensity: 0.2 });
    animate(this.ambientLight, 1, { intensity: 0.3 });
  }

  animate() {
    if(!this.scene){
      return;
    }
    requestAnimationFrame(()=>{this.animate()});
    this.render();
  }

  render() {
    this.onRender();
    this.renderer_top.render(this.scene, this.camera_top);
    this.renderer_right.render(this.scene, this.camera_right);
    this.renderer_bottom.render(this.scene, this.camera_bottom);
    this.renderer_left.render(this.scene, this.camera_left);
  }

  onRender() {
    for(let item of this.renderListenerArr) {
      item.handle();
    }
  }

  addRenderListener(obj) {
    this.renderListenerArr.add(obj);
  }

  removeRenderListener(obj) {
     this.renderListenerArr.delete(obj);
  }

  removeRenderListenerById(aId) {
    for(let item of this.renderListenerArr) {
      if( item.id == aId ) { 
        this.renderListenerArr.delete(item);
        break;
      }
    }
  }

  loadObjectByRes(arg) {
    return new Promise((resolve, rject)=> {
      let modelPath = this.modelPath + arg.name + "/";
      new THREE.OBJLoader()
        .setPath(modelPath)
        .load(arg.name + '.obj', (group) => {
          //console.log(group);
          let object = group.children[0];
          object.geometry.center();
          object.material.transparent = true;
          object.material.side = THREE.DoubleSide;
          if(arg.texture) {
            let loader = new THREE.TextureLoader();
            let texture = loader.load(this.imagePath + arg.texture, (texture)=>{});
            object.material.map = texture;
          }
          if(arg.scale) {
            object.scale.x = object.scale.y = object.scale.z = arg.scale;
          }
          resolve(object);   
        });
    })
  }

  addObjectByRes(arg) {
    return this.loadObjectByRes(arg).then(obj=>{
      this.addObject(obj);
      return obj;
    })
  }

  addObject(object, arg={}) {
    if(!arg.disableAnimate){
      let gp = object.isGroup ? object : {children: [object]};
      gp.children.forEach(o=>{
        o.material.opacity = 0;
        animate(o.material, 3, { opacity: 1 });
      })
    }
    if(arg.parent){
      arg.parent.add(object);
    }
    else {
      this.scene.add(object);  
    }
    //console.log("obj->", this.scene)
  }

  removeObject(object, arg={}) {
    let arr = [];
    if(!arg.disableAnimate){
      let gp = object.isGroup ? object : {children: [object]};
      arr = gp.children.map(o=>{
        return animate(o.material, 1, { opacity: 0 })
      })
    }
    Promise.all(arr).then(()=>{
      if(arg.parent) {
        arg.parent.remove(object);
      }
      else {
        this.scene.remove(object);
      }
    })
  }

  // startAnimate(obj, time, arg) {
  //   return animate(obj, time, arg)
  // }

  showObject(object) {
    let gp = object.isGroup ? object : {children: [object]};
    return gp.children.map(o=>{
      return animate(o.material, 1, { opacity: 1 })
    })    
  }

  hideObject(object) {
    let gp = object.isGroup ? object : {children: [object]};
    return gp.children.map(o=>{
      return animate(o.material, 1, { opacity: 0 })
    })   
  }

  removeObjectById(aId){
    let obj = this.scene.children.find(o=> o.uuid == aId);
    if(obj){
      this.removeObject(obj)
    }
  }

  destroy() {
    this.renderer_top = this.renderer_left = this.renderer_right = this.renderer_bottom = null;   
    this.camera_top = this.camera_left = this.camera_right = this.camera_bottom = null;
    while(this.scene.children.length){
      this.scene.remove(this.scene.children[0]);
    }
    this.scene = null;
    this.el = null;
  }

};