import Vue from 'vue'
import Vuex from 'vuex'
import persistedstate from "vuex-persistedstate"

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [persistedstate()],
  state: {
    mode: 1,
    speechToken: null,
  },
  mutations: {
    setMode(state, aMode) {
      state.mode = aMode;
    },
    setSpeechToken(state, aToken) {
      state.speechToken = aToken;
    }
  },
  actions: {

  }
})
