<template>
  <div class="setting">
    <img class="btn-back" @click="goBack" src="../assets/back_off.png">
    <div class="c-title"></div>
    <div class="c-setting-con">
      <h5></h5>
      <div>
        <label><input type="radio" name="displayMode" value="1" v-model="mode"  @change="onModeChange"><em>
        <img src="../assets/normal.png">
        <i class="iconfont icon-duigou1"></i></em></label>
        <label style="display:none"><input type="radio" name="displayMode" value="2" v-model="mode" @change="onModeChange"><em>
        <img src="../assets/qx.png">
        <i class="iconfont icon-duigou1"></i></em></label>
      </div>
    </div>

  </div>
</template>
<script>
export default {
  name: "setting-page",
  data() {
    return {
      mode: ""
    }
  },
  mounted() {
    this.mode = this.$store.state.mode;
  },
  methods:{
    goBack() {
      this.$router.back();
    },
    onModeChange() {
      console.log(this.mode)
      this.$store.commit("setMode", this.mode);
    }
  }
 
}

</script>

<style scoped lang="scss">
  .c-setting-con{
    h5 {
      border-bottom: solid 1px #BBBBBB;
      font-size: 5vw;
      color: #666666;
      padding: 3vw;
    }
    > div {
      text-align: center;
      label {
        color: #666666;
        font-size: 6vw;
        input {
          display: none;
          &:checked+em>i{
            display: block;
          }
        }
        em {
          position: relative;
          width: 24vw;
          height:24vw;
          margin: 0 6vw;
          font-style: normal;
          display: inline-block;
          img{
            width: 100%;
          }
          i{
            position: absolute;
            bottom: -3vw;
            right: -3vw;
            font-size: 20vw;
            color: #2274f0;
            display: none;
          }
        }
      }
      
    }
  }
  
</style>
