import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import QXContent from './views/QXContent.vue'
import NormalContent from './views/NormalContent.vue'
import Setting from './views/setting.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/qxcontent',
      name: 'qxcontent',
      component: QXContent
    },
    {
      path: '/normal-content',
      name: 'normal-content',
      component: NormalContent
    },
    {
      path: '/setting',
      name: 'setting-page',
      component: Setting
    },
    {
      path: '/about',
      name: 'about-page',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
